.slider {
  white-space: nowrap;
  scroll-behavior: smooth;
  scrollbar-width: none;
  width: 100%;
  height: 100%;
  overflow-x: scroll;
}

.slider::-webkit-scrollbar {
  display: none;
}

.slider-icon.left, .slider-icon.right {
  opacity: .2;
  cursor: pointer;
  z-index: 1;
  background: #fff;
  border-radius: 100%;
  position: absolute;
  box-shadow: 5px 5px 1.25rem #0000001f;
}

.slider-icon.left {
  left: 0;
}

.slider-icon.right {
  right: 0;
}

.slider-icon.left:hover, .slider-icon.right:hover {
  opacity: 1;
}

.slider-card-image {
